function fetchIng (){
  showPreloader()
   axios.get('https://tasty-treats-backend.p.goit.global/api/ingredients')
.then(response => {
//console.log(data) //    
const data = response.data;
ingredientsData = data;
createIngList(ingredientsData);
 
})
.catch(error => {
  console.error(' :', error);
}).finally(() => {
  hidePreloader();
}) }



//  
