 function takeIng(e) {
  try {
    const selectedOption = e.target.options[e.target.selectedIndex];
    const selectedIngredientId = selectedOption.dataset.ingredientId; //   
    const updatedData = {};
    if(e.target.name!='ingredient')
      updatedData[e.target.name] = e.target.value; 
    else 
      updatedData['ingredient'] = selectedIngredientId; //    
    Update(updatedData);
  } catch (error) {
    console.error(':', error);
  }
}

function selects(){
  addListeners();
fetchIng ();
fetchArea ();
doTime ();}

export {selects};